<!--/-->
<style lang="scss" rel="stylesheet/scss">
    @import "../../../utils/mixins/common";
    #medical-form {
        @extend %content-container;
        display: flex;
        .submit-form {
        width: 90%;
        padding: 20px;
            .select{
                width: 100%;
            }
            .subButton {
                text-align: center;
            }
        }
    }
</style>
<template>
    <main id="medical-form">
        <section class="submit-form">   
            <el-form label-width="120px" ref="form" :rules="rules" :model="fetchParam">
                <el-form-item label="" prop="course_name">
                    <el-input v-model="fetchParam.course_name"></el-input>
                </el-form-item>
                <el-form-item prop="image" label="" :label-width="formLabelWidth">
                    <div class="img-wrap" v-if="fetchParam.image">
                        <img :src="fetchParam.image | fillImgPath" width="200" height="112" alt=""/>
                    </div>
                    <ImagEcropperInput :isRound="false" :confirmFn="cropperFn"
                                       class="upload-btn"></ImagEcropperInput>
                </el-form-item>
                <el-form-item label="">
                    <vTags v-model="courseTags"></vTags>
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input v-model="fetchParam.description" type="textarea" :autosize="{ minRows: 3, maxRows: 7}" placeholder="">
                    </el-input>
                </el-form-item>
                <el-form-item prop="content" label="" id="editor" :label-width="formLabelWidth">
                    <vue-editor v-model="fetchParam.content" @ready="ueReady"></vue-editor>
                </el-form-item>
                <!-- <el-form-item label="" >
                    <div>
                    <el-button type="primary" @click="btnNextClick"></el-button>
                    </div>
                </el-form-item> -->
            </el-form>
        </section>
    </main>
</template>

<script>
    import courseService from '../../../services/course/courseService.js'
    import ImagEcropperInput from '../../component/upload/ImagEcropperInput.vue'
    import VueEditor from '../../component/form/UEditor.vue'
    import commonService from '../../../services/commonService.js'
    import vTags from '../../component/form/Tags.vue'


    function clearFn() {
        return {
            typeSelect: '',
            provinceSelect: '',
            citySelect: '',
            areaSelect: '',
            townSelect: '',
            villageSelect: '',
            name: ''
        }
    }
    export default {
        name: 'course-manage-addCourse-imgtxt',
        components: {
            ImagEcropperInput,VueEditor,vTags
        },
        data() {
            return {
                loadingData: false,
                courseTags: [],
                fetchParam: getOriginData(),
                rules: {
                    course_name:[
                        {required: true, message: '', trigger: 'blur'},
                        ,{
                            pattern:  /\S$/,
                            message: ''
                        }
                    ],
                    image: { required: true, message: '',trigger: 'change'},
                    description: { required: true, message: '',trigger: 'blur'},
                    content: { required: true, message: '',trigger: 'blur'},
            },
                formLabelWidth: '120px', // label
                changelist:{},
            }
        },
        watch:{
            'fetchParam.price'(){
                this.fetchParam.price=Number(this.fetchParam.price)
                console.log('typeof(this.fetchParam.price)===='+typeof(this.fetchParam.price));
            }
        },
        created() {
            console.log(this.$route.params.imgtxtInfo);
            
            console.log(this.$route.params.imgtxtInfo)
            xmview.setContentLoading(false)
            if(this.$route.params.handle=='edit'){ //
                courseService.getImgTxt({
                    contentid:this.$route.params.imgtxtInfo.contentid
                    }).then((ret) => {
                        xmview.setContentTile(`-`)
                        this.loadingData=false
                        this.fetchParam= this.imgtxtInfo=JSON.parse(ret)
                        this.courseTags = this.imgtxtInfo.tags ? this.imgtxtInfo.tags.split(',') : []
                    })
            }
            //       
            this.loadingData=false
        },
        methods: {
             ueReady (ue) {
                this.editor = ue
            },
             cropperFn (data, ext) {
                commonService.commonUploadImageBaseSection({
                    image: data,
                    alias: `${Date.now()}${ext}`,
                    biz: 'course',
                    extpath: 'cover',
                }).then((ret) => {
                    this.fetchParam.image = ret.url 
                    // xmview.showTip('success', '')
                    // authUtils.setAvatar(ret.data.url)
                })
            },
            getExpertsList (val, length) {
                return courseService.fetchImgTxtList({
                    name: val,
                    // category: this.type,
                    pagesize: this.pageSize,
                    page: parseInt(length / this.pageSize) + 1
                }).then((ret) => {
                    this.$emit('changelist', ret)
                    this.changelist = ret;
                    return ret
                })
            },
      
        }
    }

    function getOriginData() {
        return {
            course_name: '',
            description:'',
            type:'public',
            material_type:'article',
            image:'',
            content:''
        }
    }

</script>


